import React from "react";
import { StaticQuery, graphql } from "gatsby";
import Img from "gatsby-image";

import Helmet from "../components/Helmet";

const DiscordInvite = () => (
    <div css={{
        padding: 15,
        borderRadius: 3,
        backgroundColor: "rgba(100, 100, 100, .5)",
        textAlign: "left",
    }}>
        <div css={{
            marginBottom: 15,
            fontSize: ".7em",
            fontWeight: 500,
            textTransform: "uppercase",
            color: "lightgrey",
        }}>
            You've been invited to join Glee
        </div>
        <div css={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-between",
        }}>
            <div css={{
                display: "flex",
                alignItems: "center",
            }}>
                <img
                    alt="Icon"
                    src="https://github.com/gleeapp.png"
                    height={ 128 }
                    width={ 128 }
                    css={{
                        width: 64,
                        height: 64,
                        marginRight: 15,
                        borderRadius: 3,
                    }}
                />
                <div>
                    <div css={{ marginBottom: 5, fontWeight: 500 }}>glee.</div>
                    <div css={{ fontSize: ".8em", color: "darkgrey" }}>Discord Community</div>
                </div>
            </div>
            <a href="https://discord.gg/V3UVxa6" target="_blank" rel="noreferrer noopener">
                <button css={{
                    marginLeft: 25,
                    backgroundColor: "var(--green)",
                    fontSize: ".9em",
                    fontWeight: 500,
                }}>
                    Join
                </button>
            </a>
        </div>
    </div>
);

export default () => (
    <StaticQuery
        query={graphql`
            query {
                file(relativePath: { eq: "contact-header-image.png" }) {
                    childImageSharp {
                        fluid {
                            ...GatsbyImageSharpFluid_noBase64
                        }
                    }
                }
            }
        `}
        render={data =>
            <div css={{
                maxWidth: 720,
                margin: "0 auto",
                padding: 25,
                "p a": {
                    color: "var(--green)",
                },
            }}>
                <Helmet
                    title="Contact"
                    description="You can contact us for anything related to Glee, and we'll get back to you faster than lightning."
                />
                <Img
                    fluid={ data.file.childImageSharp.fluid }
                    style={{
                        margin: "0 auto",
                        maxWidth: 450,
                    }}
                />
                <div css={{
                    textAlign: "center",
                }}>
                    <div css={{
                        fontSize: "2em",
                        fontWeight: 700,
                        marginBottom: 25,
                    }}>
                        Contact Glee
                    </div>
                    <div css={{
                        marginBottom: 25,
                        color: "lightgrey",
                        fontWeight: 500,
                    }}>
                        You can contact us for anything related to Glee, and we'll get back to you <span title="We aren't the Flash; But as soon as humanly practical">faster than lightning</span>.
                    </div>
                    <div>
                        <div css={{ marginTop: 50 }}>
                            <div css={{
                                display: "flex",
                                justifyContent: "center",
                            }}>
                                <DiscordInvite />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        }
    />
);
